* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
img {
  width: 100px;

  display: block;
  max-width: 100%;
}

.country-list {
  list-style: none;
}

.list {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.description {
  padding-left: 10px;
}

.container {
  width: 400px;
  height: auto;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
}
